.summary-container{
  background: #222233;
  color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  height: 100vh;
}

@media (max-width: 600px){
  .summary-container{
    height: auto;
  }
}

.summary-flex-column{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.title-container{
  margin-top: 8vh;
  -ms-flex: 0.2 1;
      flex: 0.2 1
}

.summary-container h1{
  font-size: 4rem;
  text-align: center;
}

@media (max-width: 600px){
  .summary-container h1{
    font-size: 3rem;
  }
}

.summary-container a{
  color: white;
}

.contact-container{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.contact-container img{
  height: 2rem;
  width: 2rem;
  margin-right: 0.4rem;
  -webkit-filter: invert(1);
          filter: invert(1);
}

.contact-container a{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
}

.contact-container div{
  margin-right: 0.5rem;
}

@media (max-width: 600px){
  .contact-container{
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
  }

  .contact-container div{
    display: none;
  }

  .contact-container a{
    margin: 0.25rem 0;
  }
}

.summary-body{
  -ms-flex: 0.3 1;
      flex: 0.3 1;
  max-width: 900px;
}

.summary-body h2{
  margin: 1.5rem 0 1rem 0;
}

.summary-body div{
  width: 90%;
  margin: auto;
}

.skills-container{
  display: grid;
  grid-template-columns: 16% 16% 16% 16% 16% 16%;
  grid-row-gap: 1.5rem;
  justify-items: center;
}

@media (max-width: 600px){
  .skills-container{
    grid-template-columns: 33% 33% 33%;
  }
}

.skills-container div{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

.skills-container img{
  margin-bottom: 0.2rem;
  width: 5vw;
  height: 5vw;
}

@media (max-width: 600px){
  .skills-container img{
    width: 10vw;
    height: 10vw;
  }
}

.flex-end{
  margin-top: auto;
}



